<template>
    <div class="account-main">
        <div class="account-title">
            
            <span class="fr base mouseDefault" @click="goRecord"></span>
        </div>
        <div class="recharge-wrap ft14 fColor2">
            <!-- <div class="clear">
                <div class="left fl tr" style="line-height: 48px;"></div>
                <div class="right fl">
                    <div class="positionR" style="width: 360px;">
                        <div class="selected pageBack">USDT</div>
                        <span style="">
                            <img src="@/assets/images/register_icon_down.png" >
                            <img src="@/assets/images/register_icon_up.png" style="display: none;">
                            </span>
                        <ul class="pageBack" style="display: none;">
                            <li >
                                USDT
                            </li>
                            <li >
                                JNB
                            </li>
                            <li >
                                ETH
                            </li>
                        </ul> <input type="hidden" disabled="disabled" value="USDT"></div>
                </div>
            </div> -->
            <div class="mt40 clear">
                <div class="left fl tr"></div>
                <div class="right fl">
                    <img :src="url" class="qrcode">
                    <p class="ft20 fColor1 mt15">
                        <span id="copy-text">{{addr}}</span>
                        <span id="copy" data-clipboard-action="copy" data-clipboard-target="#copy-text" class="base ml20 f14 mouseDefault"></span>
                    </p>
                </div>
            </div>
            <div class="tip">
                <p></p>
                <p>  JNB </p>
                <p> 610-60</p>
                <p> USDTsimple sendsend all</p>
                <p> 16</p>
                <p>  100 USDT</p>
                <p> </p>
                <p> </p>
            </div>
            <div class="tip" style="display: none;">
                <p></p>
                <p> USDT</p>
                <p> 1530</p>
                <p>  0.01 USDT </p>
                <p> (Coinbase)</p>
                <p> </p>
                <p> </p>
            </div>
            <div class="tip" style="display: none;">
                <p></p>
                <p>  USDT </p>
                <p> 16</p>
                <p>  0.01 USDT </p>
                <p> </p>
                <p> </p>
                <p> </p>
            </div>
        </div>
    </div>
</template>
<script>
import indexHeader from '@/view/indexHeader'
import left from '@/view/left'
import "@/lib/clipboard.min.js"
export default {
    name:'recharge',
    data(){
        return{
            addr:'',
            url:''
        }
    },
    components:{
        indexHeader,
        left
    },
    methods:{
        goRecord(){
            this.$router.push({name:'coinRecord'})
        },
        init(){
             var clipboard = new Clipboard('#copy')
            clipboard.on('success', function (e) {
                alert('')
            });
            clipboard.on('error', function (e) {
                alert('')
            });
        }
    },
    created(){
        this.address=localStorage.getItem('address') || '';
        if(this.address){
            this.$http({
                url:'/api/'+'money/rechange?user_id='+this.address,
                type:'GET'
            }).then(res=>{
                this.addr=res.data.message.company_eth_address;
                this.url='http://qr.liantu.com/api.php?&w=300&text='+res.data.message.company_eth_address;
                var content = this.addr;
                var clipboard = new Clipboard('#copy')
            }).catch(error=>{
                return error
            })
        }
    },
    mounted(){
        this.init();
    }
};
</script>
<style scoped lang="scss">
.box{
    background-color: #273041;
}
.box .account{
    width: 1500px;
    padding-top: 30px;
    min-height: 880px;
    margin: 0px auto;
    overflow: hidden;
}
.recharge-wrap {
    padding: 70px 0;
    .left {
      width: 104px;
      margin-right: 26px;
    }
    .right {
      .qrcode {
        width: 142px;
        height: 142px;
      }
    }
    .tip {
      margin-top: 122px;
      padding-left: 50px;
      line-height: 26px;
    }
  }

.selected{
    height: 48px;
    border: 1px solid #2e3753;
    padding: 0 40px 0 18px;
    color: #cdd6e4;
    border-radius: 3px;
    line-height: 48px;
    word-break: keep-all;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    
}
.positionR img{
    position: absolute;
    right: 15px;
    top: 20px;
}
</style>





